.login-container {
  .preloader {
    align-items: center;
    background: rgb(23, 22, 22);
    display: flex;
    height: 100vh;
    justify-content: center;
    left: 0;
    position: fixed;
    top: 0;
    transition: opacity 0.3s linear;
    width: 100%;
    z-index: 9999;
    opacity: 0.5;
  }

  .main {
    box-shadow: 0px 0px 8px 5px #807f7f;
    margin: 8%;
    .left-panel {
      width: 50%;
      height: 550px;
      img {
        width: 75%;
        height: 100%;
      }
      .login-header {
        margin-top: 30%;
        margin-left: 10%;
        margin-right: 10%;
      }
    }
    .right-panel {
      width: 50%;
      height: 550px;
      background-color: #e8fffe;
      .login-header {
        margin: 15% 5% 10% 5%;
      }
      #customBtn {
        margin: auto;
        box-shadow: 0px 0px 8px 5px #807f7f;
        .google-icon {
          font-size: 25px;
        }
        span {
          padding-left: 35px;
        }
      }
    }

    .login-info {
      margin: 10%;
    }

    .sign-in {
      display: flex;
      justify-content: space-between;
      /* border: 1px solid gray; */
      background: white;
      background-color: #fff;
      color: #757575;
      -webkit-border-radius: 1px;
      border-radius: 1px;
      -webkit-box-shadow: 0 2px 4px 0px rgba(0, 0, 0, 0.25);
      box-shadow: 0 2px 4px 0 rgb(0, 0, 0/25%);
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      -webkit-transition: background-color 0.218s, border-color 0.218s,
        box-shadow 0.218s;
      transition: background-color 0.218s, border-color 0.218s,
        box-shadow 0.218s;
      user-select: none;
      -webkit-user-select: none;
      -webkit-appearance: none;
      background-color: #fff;
      background-image: none;
      color: #262626;
      cursor: pointer;
      outline: none;
      overflow: hidden;
      position: relative;
      text-align: center;
      vertical-align: middle;
      white-space: nowrap;
      width: auto;
    }
  }
}
